.add-player .card-body {
  display: none;
}

.player-list-addheading {
  cursor: pointer;
  user-select: none;
}

.player-list {
  font-size: 1.1em;
}

.player-list .player-info {
  border-bottom: .5px dotted rgb(0, 0, 0);
}

.player-list .player-name:last-child {
  border-bottom: none;
}

.player-list .player-name {
  font-weight: 600;
  color: #fb4f14;
  font-size: 1.3em;
  line-height: 100%;
}

.player-list .label-item {
  font-weight: 600;
  color: #000000;
}

.player-list .game-date {
  font-style: italic;
}

.player-list .player-notes {
  line-height: 120%;
}

.dropdown-item.active,
.dropdown-item:active {
  background-color: #fb4f14;
}